import { flatFieldsToSearch } from "../../config";

const FILTER_IDS_BASED_ON_SEARCH_BOX = (state, action) => {
  let unfilteredData = [...state.unfilteredData];

  const keywords = state.searchString.split(" ");

  let filteredBySearchString = unfilteredData;

  for (const keyword of keywords) {
    filteredBySearchString = filterByKeyWord(
      keyword.toLowerCase(),
      filteredBySearchString,
      flatFieldsToSearch
    );
  }

  return {
    ...state,
    filteredIdsBasedOnSearchBox: filteredBySearchString.map(
      (datum) => datum.uid
    ),
  };
};

export default FILTER_IDS_BASED_ON_SEARCH_BOX;

export const filterByKeyWord = (keyword, inputData, flatFieldsToSearch) => {
  let outputData = [];
  for (const datum of inputData) {
    let shouldIncludeDatum = false;
    for (const field of flatFieldsToSearch) {
      let value = datum[field];

      if (typeof value === "number") {
        value = value.toString();
      }

      try {
        if (value) {
          value = value.toLowerCase();

          if (value.includes(keyword)) {
            shouldIncludeDatum = true;
            if (field === "flatLookupString") {
              console.log({ field, value, keyword });
            }
          }
        }
      } catch (error) {}
    }
    if (shouldIncludeDatum) {
      outputData.push(datum);
    }
  }

  return outputData;
};
