const UPDATE_SEARCH_RESULTS = (state, action) => {
  const idsBasedOnSearchBox = [...state.filteredIdsBasedOnSearchBox];

  const filteredData = state.unfilteredData.filter((datum) =>
    idsBasedOnSearchBox.includes(datum.uid)
  );

  return {
    ...state,
    filteredData,
  };
};

export default UPDATE_SEARCH_RESULTS;
