import React, { Fragment, useEffect } from "react";

import { useSelector, useDispatch } from "react-redux";

import NavBar from "./Components/NavBar";
import SearchBox from "./Components/SearchBox";
import Table from "./Components/Table";
import Paginator from "./Components/Paginator";

import { columnsOnMainTable } from "./config";

function App() {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({ type: "ADD_UID_TO_RAW_DATA" });
    dispatch({ type: "INITIALIZE_DATA" });
    dispatch({ type: "INITIALIZE_NUM_PAGES" });
    dispatch({ type: "INITIALIZE_RESULTS_IN_VIEW" });
  }, []);

  const resultsInView = useSelector((state) => state.resultsInView);

  const filteredData = useSelector((state) => state.filteredData);
  return (
    <Fragment>
      <NavBar></NavBar>

      <div className="container">
        <div className="row">
          <div className="col s4">
            <SearchBox></SearchBox>
          </div>
          <div className="col s8">
            <div className="card">
              <div className="card-content">
                <Table
                  data={resultsInView}
                  colNames={columnsOnMainTable}
                ></Table>
              </div>
            </div>
            <Paginator></Paginator>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default App;
