const HANDLE_PREV_PAGE_CLICK = (state, action) => {
  if (state.pageNumber > 1) {
    const newPageNumber = state.pageNumber - 1;
    const newStartIndex = (newPageNumber - 1) * state.resultsPerPage + 1;
    const newEndIndex = Math.min(
      newPageNumber * state.resultsPerPage,
      state.filteredData.length
    );
    const newResultsInView = state.filteredData.slice(
      newStartIndex - 1,
      newEndIndex
    );
    return {
      ...state,
      pageNumber: newPageNumber,
      startIndex: newStartIndex,
      endIndex: newEndIndex,
      resultsInView: newResultsInView,
    };
  } else {
    return state;
  }
};

export default HANDLE_PREV_PAGE_CLICK;
