import { useSelector, useDispatch } from "react-redux";

const Paginator = () => {
  const dispatch = useDispatch();
  const pageNumber = useSelector((state) => state.pageNumber);
  const numPages = useSelector((state) => state.numPages);
  const startIndex = useSelector((state) => state.startIndex);
  const endIndex = useSelector((state) => state.endIndex);
  const filteredData = useSelector((state) => state.filteredData);

  const renderPaginator = () => {
    return (
      <div className="row">
        <div className="col s4 center">
          <ul className="pagination">
            <li>
              <a
                href="#!"
                onClick={() =>
                  dispatch({
                    type: "HANDLE_FIRST_PAGE_CLICK",
                  })
                }
                style={{ padding: "0px" }}
              >
                <i className="material-icons">first_page</i>
              </a>
              <a
                href="#!"
                onClick={() =>
                  dispatch({
                    type: "HANDLE_PREV_PAGE_CLICK",
                  })
                }
                style={{ padding: "0px" }}
              >
                <i className="material-icons">chevron_left</i>
              </a>
            </li>
            <li className="active grey darken-1">
              <a href="#!">{pageNumber}</a>
            </li>

            <li>
              <a
                href="#!"
                onClick={() =>
                  dispatch({
                    type: "HANDLE_NEXT_PAGE_CLICK",
                  })
                }
                style={{ padding: "0px" }}
              >
                <i className="material-icons">chevron_right</i>
              </a>
            </li>

            <li>
              <a
                href="#!"
                onClick={() =>
                  dispatch({
                    type: "HANDLE_LAST_PAGE_CLICK",
                  })
                }
                style={{ padding: "0px" }}
              >
                <i className="material-icons">last_page</i>
              </a>
            </li>
          </ul>
        </div>

        <div className="col s8 center">
          <p>
            On page {pageNumber} of {numPages}, viewing {startIndex} to{" "}
            {Math.min(endIndex, filteredData.length)} of {filteredData.length}{" "}
            results.
          </p>
        </div>
      </div>
    );
  };

  return renderPaginator();
};

export default Paginator;
