const UPDATE_SEARCH_RESULTS = (state, action) => {
  const pageNumber = 1;
  const startIndex = 1;
  const endIndex = Math.min(state.resultsPerPage, state.filteredData.length);
  const resultsInView = state.filteredData.slice(startIndex - 1, endIndex);
  return {
    ...state,
    pageNumber,
    startIndex,
    endIndex,
    resultsInView,
  };
};

export default UPDATE_SEARCH_RESULTS;
