import { data, defaultResultsPerPage } from "../config";

import ADD_UID_TO_RAW_DATA from "./Actions/ADD_UID_TO_RAW_DATA";
import INITIALIZE_DATA from "./Actions/INITIALIZE_DATA";
import FILTER_IDS_BASED_ON_SEARCH_BOX from "./Actions/FILTER_IDS_BASED_ON_SEARCH_BOX";
import UPDATE_SEARCH_RESULTS from "./Actions/UPDATE_SEARCH_RESULTS";
import HANDLE_NEXT_PAGE_CLICK from "./Actions/HANDLE_NEXT_PAGE_CLICK";
import HANDLE_PREV_PAGE_CLICK from "./Actions/HANDLE_PREV_PAGE_CLICK";
import RESET_RESULTS_IN_VIEW from "./Actions/RESET_RESULTS_IN_VIEW";
import HANDLE_FIRST_PAGE_CLICK from "./Actions/HANDLE_FIRST_PAGE_CLICK";
import HANDLE_LAST_PAGE_CLICK from "./Actions/HANDLE_LAST_PAGE_CLICK";

const initialState = {
  // data (unfiltered)
  unfilteredData: data,

  // state related to search box
  searchString: "",

  // state used to create filteredData
  filteredIdsBasedOnSearchBox: [],

  // data (filtered based on search input)
  filteredData: [],

  // state related to the paginator
  pageNumber: 1,
  startIndex: 1,
  endIndex: defaultResultsPerPage,

  // data (currently in view)
  resultsInView: [],

  resultsPerPage: defaultResultsPerPage,
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case "ADD_UID_TO_RAW_DATA":
      return ADD_UID_TO_RAW_DATA(state, action);

    case "INITIALIZE_DATA":
      return INITIALIZE_DATA(state, action);

    case "SEARCH_BOX_CHANGE":
      return {
        ...state,
        searchString: action.payload,
      };

    case "FILTER_IDS_BASED_ON_SEARCH_BOX":
      return FILTER_IDS_BASED_ON_SEARCH_BOX(state, action);

    case "UPDATE_SEARCH_RESULTS":
      return UPDATE_SEARCH_RESULTS(state, action);

    case "HANDLE_PREV_PAGE_CLICK":
      return HANDLE_PREV_PAGE_CLICK(state, action);

    case "HANDLE_NEXT_PAGE_CLICK":
      return HANDLE_NEXT_PAGE_CLICK(state, action);

    case "INITIALIZE_NUM_PAGES":
      return {
        ...state,
        numPages: Math.ceil(state.unfilteredData.length / state.resultsPerPage),
      };

    case "INITIALIZE_RESULTS_IN_VIEW":
      return {
        ...state,
        resultsInView: state.unfilteredData.slice(0, state.resultsPerPage),
      };

    case "RESET_RESULTS_IN_VIEW":
      return RESET_RESULTS_IN_VIEW(state, action);

    case "RECOMPUTE_NUM_PAGES":
      return {
        ...state,
        numPages: Math.ceil(state.filteredData.length / state.resultsPerPage),
      };

    case "HANDLE_FIRST_PAGE_CLICK":
      return HANDLE_FIRST_PAGE_CLICK(state, action);

    case "HANDLE_LAST_PAGE_CLICK":
      return HANDLE_LAST_PAGE_CLICK(state, action);
    default:
      return state;
  }
}
